@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

.App {
    text-align: center;
}

.appIntro {
    margin: 3.5rem 7rem 0rem 7rem;
    text-align: justify;
    font-size: 2.4rem;
}

.menuItem {
    display: block;
    padding: 0.5rem;
    font-size: 2.5rem;
    font-weight: bolder;
    color: #66a466;
    text-align: right;
    float: left;
    font-family: 'Nunito', sans-serif;
}

.menuItem:hover {
    font-size: 2.5rem;
    font-weight: bolder;
    background-color: #fff;
    padding: 0.5rem;
}

body {
    color: rgb(240, 248, 255);
}

.headerStyle {
    margin-bottom: 5rem;
    padding: 0rem 0rem 4.2rem 0rem;
    background-color: rgb(25, 24, 24);
}

.mainIcon {
    width: 8rem;
    height: 5rem;
    padding: -1px;
    margin: -1rem;
}

.abtPageRes {
    color: #66a466;
    text-decoration: none;
}

img,
.autogenImg {
    width: 30rem;
    height: 14rem;
    color: #000;
    margin: 0.5rem;
    border-radius: 25px;
    text-align: center;
    font-size: 2rem;
}

h1 {
    color: #000;
}

.AboutPageStyle {
    padding: 10px;
    text-align: center;
}
/*
=======================================================================
                  CSS styles for displayed data
=======================================================================
*/

.postBox {
    padding-top: 20px;
    width: 35rem;
    height: 25rem;
    border: 3px solid #000;
    margin: 0.5rem 0.7rem 0rem 0rem;
    border-radius: 25px;
    text-align: center;
}

.postBoxProgpage {
    flex-direction: column;
    width: 35rem;
    height: 15rem;
    margin: 0.5rem;
    padding: 1.5rem;
    border: 3px solid #000;
    border-radius: 25px;
    text-align: center;
}

.postTitle {
    color: #000000;
    font-family: 'Nunito', sans-serif;
    font-size: 1.5rem;
    text-align: justify;
    white-space: normal;
    z-index: 1;
    padding: 0rem 1.5rem;
}

.postTitle a,
a {
    text-decoration: none;
}

.postAuthorStyle,
.postUpvoteStyle {
    font-size: 1.2rem;
}

.SearchBar {
    margin: auto;
    width: 70%;
    padding: 1.8rem;
    height: 2rem;
    font-size: 2.5rem;
    box-shadow: 0px 5px 25px rgba(129, 129, 129, 0.2);
    backdrop-filter: blur(16px) saturate(180%);
    -webkit-backdrop-filter: blur(16px) saturate(180%);
    background-color: rgba(255, 255, 255, 0.75);
    border-radius: 500px;
    border: 4.5px solid rgba(0, 0, 0, 0.3);
}

.aboutSect {
    float: right;
}
